import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { IOptionsPsSweepingAndPoolingListExposed } from './ps-sweeping-and-pooling-list.component.interface';

@Component({
  selector: 'ps-sweeping-and-pooling-list',
  templateUrl: './ps-sweeping-and-pooling-list.component.html',
  styleUrls: ['./ps-sweeping-and-pooling-list.component.scss'],
})
export class PsSweepingAndPoolingListComponent extends PsTemplateListPage implements OnInit {

  @Input() options: IOptionsPsSweepingAndPoolingListExposed;

  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  @Output() public onClickCard = new EventEmitter<any>();
  @Output() reloadFct = new EventEmitter<string>();
  constructor() {
    super();
  }

  ngOnInit() {
    super.init();
    if (this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
  }

  getContainerLookUpOptions(val) {
    return this.prepareLookUp(val);
  }

  private prepareLookUp(card: any): any {
    let lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelsValueMap = this.populateSweepingAndPoolingMap(card);
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.labelKey = this.options.labelKey;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.fcName = this.options.fcName;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.requestMap = this.requestMap;
    lookupDetails.actionDetailsOptions = [];
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }

    if (card.instructionTypeCode === CommonBussinessConstant.INSTRUCTION_TYPE_MIN) {
      lookupDetails.showInitialCardValues = 6;
    } else {
      lookupDetails.showInitialCardValues = 5;
    }

    lookupDetails.showInitialCardValues = card.instructionMethodCode === CommonBussinessConstant.INSTRUCTION_METHOD_OFFLINE_CODE ? lookupDetails.showInitialCardValues + 1 : lookupDetails.showInitialCardValues
    lookupDetails.actionDetailsOptions = [];

    // salah to see the link change the if to true
    if ((card.statusCode === CommonBussinessConstant.SWEEPING_AND_POOLING_STATE_ACTIVE) || (card.statusCode === CommonBussinessConstant.SWEEPING_AND_POOLING_STATE_APPROVED) || (card.statusCode === CommonBussinessConstant.SWEEPING_AND_POOLING_STATE_TO_BE_STOPPED)) {
      lookupDetails.pageData = {
        operID: ConstantCommon.VIEW_STATEMENT_OF_TRANSACTIONS,
        title: 'sweeping_and_pooling_report_key'
      };
      lookupDetails.statementOptions = {
        buttonIcon: 'account-statement',
        redirectUrl: 'sweeping-list'
      };
      lookupDetails.actionDetailsOptions.push({
        id: 'edit',
        label: 'edit_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'edit1',
            labelOptions: {
              labelKey: 'edit_key'
            }
          },
          route: 'sweeping-and-pooling',
          navigationOptions: {
            queryParams: {
              readOnlypage: false,
              instructionType: card.instructionType,
              instructionMethod: card.instructionMethod,
              isEditScreen: true
            },
          },
          pageOptions: {
            operId: CommonBussinessConstant.SWEEPING_AND_POOLING_LIST_EDIT_OPER_ID,
            iconName: 'edit1',
            title: 'edit_key'
          }
        },
        cssClass: 'edit-benef local-trasnfer-actions',
      }, {
        id: 'stop',
        buttonIcon: 'square-outline',
        labelOptions: {
          labelKey: 'stop_key'
        },
        actionUrl: PsCommonBusinessSettings.serviceUrl.stopSweepPoolEndPoint,
        cssClass: 'close-sweep fab-edit-button',
        label: 'stop_key',
        iconName: 'square-outline',
        actionType: 'delete',
        alertType: 'prompt',
        alertStyle: 'info',
        alertLabel: 'are_you_sure_you_want_to_stop_this_sweeping_and_pooling_record_key',
        reloadList: true,
        rquestParam: [],
        operId: CommonBussinessConstant.SWEEPING_AND_POOLING_LIST_STOP_OPER_ID
      });
    }
    // salah to see the link change the if to true
    if (card.statusCode === CommonBussinessConstant.SWEEPING_AND_POOLING_STATE_STOPPED) {
      lookupDetails.actionDetailsOptions.push({
        id: 'close',
        buttonIcon: 'close',
        labelOptions: {
          labelKey: 'close_key'
        },
        actionUrl: PsCommonBusinessSettings.serviceUrl.closeSweepPoolEndPoint,
        cssClass: 'close-sweep fab-edit-button',
        label: 'close_key',
        iconName: 'close',
        actionType: 'delete',
        alertType: 'prompt',
        alertStyle: 'info',
        alertLabel: 'are_you_sure_you_want_to_delete_this_sweeping_and_pooling_record_key',
        reloadList: true,
        rquestParam: [],
        operId: CommonBussinessConstant.SWEEPING_AND_POOLING_LIST_CLOSE_OPER_ID
      }, {
        id: 'resume',
        buttonIcon: 'play',
        labelOptions: {
          labelKey: 'resume_key'
        },
        actionUrl: PsCommonBusinessSettings.serviceUrl.resumeSweepPoolEndPoint,
        cssClass: 'close-sweep fab-edit-button',
        label: 'resume_key',
        iconName: 'play',
        actionType: 'submit',
        alertType: 'prompt',
        alertStyle: 'info',
        alertLabel: 'are_you_sure_you_want_to_resume_this_sweeping_and_pooling_record_key',
        reloadList: true,
        rquestParam: [],
        operId: CommonBussinessConstant.SWEEPING_AND_POOLING_LIST_RESUME_OPER_ID
      });
    }

    return lookupDetails;
  }
  reloadList() {
    this.reloadFct.emit();
  }

  private populateSweepingAndPoolingMap(card) {
    let labelValuesMap = new Map<string, IMapKeyValue>();

    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    // main details
    let instructionMethodMap: IMapKeyValue = {};
    let instructionTypeMap: IMapKeyValue = {};
    let targetAccountMap: IMapKeyValue = {};
    let coveringReceivingAccountMap: IMapKeyValue = {};
    let amountMap: IMapKeyValue = {};
    let periodicityMap: IMapKeyValue = {};
    // additional fields
    let numberOfPaymentsMap: IMapKeyValue = {};
    let scheduledDateMap: IMapKeyValue = {};



    instructionMethodMap = {
      key: 'instructMethod',
      value: '------',
      isEdit: false,
    };
    instructionTypeMap = {
      key: 'instructionType',
      value: '------',
      isEdit: false,
    };
    targetAccountMap = {
      key: 'targetAcc',
      value: '--- --- --- ---',
      isEdit: false,
    };
    coveringReceivingAccountMap = {
      key: 'coveringReceivingAcc',
      value: '--- --- --- ---',
      isEdit: false,
    };
    amountMap = {
      key: 'transactionAmount',
      value: '----',
      isEdit: false,
    };
    periodicityMap = {
      key: 'periodicityDesc',
      value: '----',
      isEdit: false,
    };
    numberOfPaymentsMap = {
      key: 'noOfPayments',
      value: '-',
      isEdit: false,
    };
    scheduledDateMap = {
      key: 'scheduledDates',
      value: '--/--/----',
      isEdit: false,
    };

    headerTitleMap = {
      key: 'title',
      value: 'transactionNumber', //referenceNo
      isEdit: false,
    };


    labelValuesMap.set('instruction_method_key', instructionMethodMap);
    labelValuesMap.set('instruction_type_key', instructionTypeMap);
    labelValuesMap.set('target_account_key', targetAccountMap);
    labelValuesMap.set('convering_receiving_account_key', coveringReceivingAccountMap);
    if (card.instructionTypeCode === CommonBussinessConstant.INSTRUCTION_TYPE_MIN || card.instructionTypeCode === CommonBussinessConstant.INSTRUCTION_TYPE_MAX) {
      labelValuesMap.set('amount_key', amountMap);
    }
    if (card.instructionMethodCode === CommonBussinessConstant.INSTRUCTION_METHOD_OFFLINE_CODE) {
      labelValuesMap.set('periodicity_key', periodicityMap);
      labelValuesMap.set('no_of_payments_key', numberOfPaymentsMap);
    }
    labelValuesMap.set('scheduled_date_key', scheduledDateMap);

    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);

    return labelValuesMap
  }

  updateCard(card: any) {
    if (card && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === card);
      this.options.item = this.options.itemList[0];
      this.options.showItemPopUp = !this.options.showItemPopUp;
      // this.options.showSelectedCard = false;
    }
    this.onClickCard.emit(this.options);
  }
}
